'use client';

import ProjectComponent from "@/app/components/projects/ProjectComponent";
import useEmblaCarousel from "embla-carousel-react";
import { useState } from "react";

const projects = [
    {
        title: "Sistema de Facturación REST API",
        description: "API REST para gestión de facturación empresarial desarrollada con Spring Boot (Java 21) y MySQL. Permite a empleados gestionar clientes, productos y facturas con líneas de detalle.\n" +
            "\n" +
            "Características principales\n" +
            "Gestión multi-usuario: cada empleado administra su cartera de clientes\n" +
            "\n" +
            "Emisión de facturas: numeración automática (YYYY-0001), estados (DRAFT/SENT/PAID/OVERDUE/CANCELLED)\n" +
            "\n" +
            "Catálogo de productos: clasificación por tipo (HARDWARE/SOFTWARE/INSTALLATION/ASSEMBLE), control de stock y precios\n" +
            "\n" +
            "Arquitectura en capas: Controller, Service, Repository, Entity\n",
        tech: "Spring Boot, Spring Data JPA, MySQL, Hibernate",
        url: "https://github.com/Exhodus/FacturationAPI"
    }
]
export default function ProjectPage(){



    return(
        <div className="relative w-full flex flex-col items-center gap-8">
            {/* Carousel */}
            <div className="overflow-hidden w-full" ref={emblaRef}>
                <div className="flex">
                    {projects.map((project, idx) => (
                        <div key={idx} className="flex-[0_0_100%] flex justify-center">
                            <ProjectCard {...project} />
                        </div>
                    ))}
                </div>
            </div>

            {/* Controles */}
            <div className="flex gap-4">
                <button
                    onClick={scrollPrev}
                    className="px-6 py-2 bg-surface border-2 border-primary text-primary rounded-lg hover:bg-primary hover:text-background transition"
                >
                    ← Anterior
                </button>
                <button
                    onClick={scrollNext}
                    className="px-6 py-2 bg-surface border-2 border-primary text-primary rounded-lg hover:bg-primary hover:text-background transition"
                >
                    Siguiente →
                </button>
            </div>
        </div>
    )
}